import LineChart from '@/components/LineChart/LineChart';
import autotime from '@/utils/autotime';
import request from '@/utils/request';

var moment = require('moment');

export default {
  components: {
    LineChart: LineChart
  },
  data: function data() {
    return {
      nowTime: moment(new Date()).format('YYYY-MM-DD HH:mm:ss'),
      currentId: 0,
      config: [{
        index: 'pv_goods',
        title: '商品浏览量',
        value: 0,
        hb: 0
      }, {
        index: 'uv_goods',
        title: '商品访客数',
        value: 0,
        hb: 0
      }, {
        index: 'pv_collect',
        title: '商品收藏数',
        value: 0,
        hb: 0
      }, {
        index: 'pv_pay',
        title: '商品订单数',
        value: 0,
        hb: 0
      }, {
        index: 'gmv_fee',
        title: '商品支付金额',
        value: 0,
        hb: 0
      }],
      lineChartData: {
        tit: '',
        actualData: [],
        xdate: [],
        date: ''
      },
      lineChartArr: {},
      loading: false,
      dates: [new Date(), new Date()],
      pickerOptions: {
        shortcuts: [{
          text: '昨天',
          onClick: function onClick(picker) {
            picker.$emit('pick', [autotime.yday, autotime.yday]);
          }
        }, {
          text: '7天',
          onClick: function onClick(picker) {
            picker.$emit('pick', [autotime.y6day, autotime.date]);
          }
        }, {
          text: '30天',
          onClick: function onClick(picker) {
            picker.$emit('pick', [autotime.y29day, autotime.date]);
          }
        }]
      }
    };
  },
  created: function created() {
    autotime.reloadTime();
    this.dates = [autotime.date, autotime.date];
    this.changeChooseDate();
  },
  methods: {
    choose: function choose(i) {
      this.currentId = i;
      this.lineChartData.tit = this.config[i].title;
      this.lineChartData.actualData = this.lineChartArr[this.config[i].index];
    },
    changeChooseDate: function changeChooseDate() {
      var _this = this;

      var mts = moment(this.dates[0]);
      var mte = moment(this.dates[1]);
      this.loading = true;
      request({
        url: '/app/business/appIndex',
        method: 'post',
        data: {
          gid: 'all',
          cmp_period: true,
          indices: ['pv_goods', 'uv_goods', 'pv_collect', 'pv_pay', 'gmv_fee'],
          sts: mts.unix(),
          ets: mte.unix() + 86400
        }
      }).then(function (res) {
        _this.loading = false;

        _this.config.forEach(function (v, i) {
          _this.config[i].value = Math.round(res.data[_this.config[i].index] || 0);
          _this.config[i].pre = Math.round(res.data[_this.config[i].pre] || 0);
          _this.config[i].hb = autotime.cmpWithPoint2(_this.config[i].value, _this.config[i].pre);
        });

        _this.getChartData(mts, mte);
      }).catch(function (e) {
        _this.loading = false;
      });
    },
    getChartData: function getChartData(mts, mte) {
      var _this2 = this;

      var data = {
        gid: 'all',
        indices: ['pv_goods', 'uv_goods', 'pv_collect', 'pv_pay', 'gmv_fee'],
        sts: mts.unix(),
        ets: mte.unix() + 86400
      };

      if (mts.isSame(mte)) {
        data.time_group = 'hh';
        this.lineChartData.xdate = autotime.hours;
      } else if (mts.isBefore(mte)) {
        data.time_group = 'ds';
        this.lineChartData.xdate = [];

        while (!mts.isAfter(mte)) {
          this.lineChartData.xdate.push(mts.format('YYYY-MM-DD'));
          mts.add(1, 'd');
        }
      } else {
        return;
      }

      this.loading = true;
      request({
        url: '/app/business/appIndex',
        method: 'post',
        data: data
      }).then(function (res) {
        _this2.loading = false;

        if (data.time_group === 'hh') {
          _this2.lineChartArr = {};
          var result = res.data[mts.format('YYYY-MM-DD')];
          data.indices.forEach(function (v) {
            _this2.lineChartArr[v] = [];

            _this2.lineChartData.xdate.forEach(function (h, i) {
              _this2.lineChartArr[v].push(result && result[i.toString()] && result[i.toString()][v] || 0);
            });
          });
        } else {
          _this2.lineChartArr = {};
          data.indices.forEach(function (v) {
            _this2.lineChartArr[v] = [];

            _this2.lineChartData.xdate.forEach(function (i) {
              _this2.lineChartArr[v].push(res.data[i] && res.data[i][v] || 0);
            });
          });
        }

        _this2.choose(_this2.currentId);
      }).catch(function (e) {
        _this2.loading = false;
      });
    }
  }
};